"use client"

import { useState } from "react"
import { Menu, X } from "lucide-react"

export default function Navbar({ activeSection, setActiveSection }) {
  const [isOpen, setIsOpen] = useState(false)

  const navItems = [
    { label: "Home", id: "home" },
    { label: "Cultural Points", id: "points" },
    { label: "Events", id: "events" },
    { label: "Gallery", id: "gallery" },
    { label: "Team", id: "team" },
  ]

  const handleNavClick = (id) => {
    setActiveSection(id)
    setIsOpen(false)
  }

  return (
    <nav className="sticky top-0 z-50 bg-transparent backdrop-blur-sm border-b-2 border-accent/30">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16 sm:h-20">
          <div className="text-2xl sm:text-3xl font-bold text-white carnival-font drop-shadow-lg">INSIGHT</div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex gap-8">
            {navItems.map((item) => (
              <button
                key={item.id}
                onClick={() => handleNavClick(item.id)}
                className={`transition-all duration-300 font-semibold text-sm drop-shadow-md ${
                  activeSection === item.id ? "text-accent scale-110" : "text-white hover:text-accent"
                }`}
              >
                {item.label}
              </button>
            ))}
          </div>

          {/* Mobile Menu Button */}
          <button className="md:hidden text-white p-2 drop-shadow-lg" onClick={() => setIsOpen(!isOpen)}>
            {isOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isOpen && (
          <div className="md:hidden pb-4 space-y-2 border-t border-accent/30 bg-black/20 backdrop-blur-sm">
            {navItems.map((item) => (
              <button
                key={item.id}
                onClick={() => handleNavClick(item.id)}
                className={`block w-full text-left px-4 py-2 rounded transition-all ${
                  activeSection === item.id ? "bg-accent text-primary font-bold" : "text-white hover:bg-accent/20"
                }`}
              >
                {item.label}
              </button>
            ))}
          </div>
        )}
      </div>
    </nav>
  )
}
