"use client"

import { useState, useRef } from "react"
import { ChevronLeft, ChevronRight, X } from "lucide-react"
import { galleryData } from "../data/gallery"

export default function GalleryRollercoaster() {
  const [selectedImage, setSelectedImage] = useState(null)
  const [scrollPos, setScrollPos] = useState(0)
  const scrollContainerRef = useRef(null)

  const scroll = (direction) => {
    const container = scrollContainerRef.current
    const scrollAmount = 320
    const targetPos = scrollPos + (direction === "left" ? -scrollAmount : scrollAmount)
    container.scrollTo({ left: targetPos, behavior: "smooth" })
    setScrollPos(targetPos)
  }

  return (
    <section className="py-16 sm:py-20 px-4 bg-gradient-to-r from-primary via-secondary to-accent/20">
      <div className="max-w-7xl mx-auto">
        {/* Title */}
        <h2 className="text-4xl sm:text-5xl font-bold text-white text-center mb-12 carnival-font drop-shadow-lg">
          Gallery Rollercoaster
        </h2>

        {/* Gallery Container with Track */}
        <div className="relative">
          {/* Track Line */}
          <div className="absolute left-0 right-0 top-1/2 h-1 bg-gradient-to-r from-accent via-yellow-400 to-accent opacity-30 -translate-y-1/2 pointer-events-none"></div>

          <div className="flex items-center gap-4">
            {/* Left Button */}
            <button
              onClick={() => scroll("left")}
              className="flex-shrink-0 bg-accent hover:bg-accent-dark text-primary p-3 rounded-full shadow-lg transition-all"
            >
              <ChevronLeft size={24} />
            </button>

            {/* Scrollable Container */}
            <div
              ref={scrollContainerRef}
              className="flex-1 overflow-x-auto scroll-smooth"
              style={{ scrollBehavior: "smooth" }}
            >
              <div className="flex gap-6 pb-4">
                {galleryData.map((image, idx) => (
                  <button
                    key={image.id}
                    onClick={() => setSelectedImage(image)}
                    className="flex-shrink-0 group relative"
                    style={{
                      transform: `translateY(${Math.sin(idx * 0.5) * 10}px)`,
                    }}
                  >
                    <div className="w-56 h-56 bg-gray-200 rounded-lg shadow-lg overflow-hidden border-4 border-white hover:border-accent transition-colors">
                      <img
                        src={image.url || "/placeholder.svg"}
                        alt={image.alt}
                        className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-300"
                        onError={(e) => {
                          e.target.src = `/placeholder.svg?height=224&width=224&query=${image.alt}`
                        }}
                      />
                    </div>
                    <div className="absolute inset-0 bg-black/0 group-hover:bg-black/30 transition-colors rounded-lg flex items-center justify-center opacity-0 group-hover:opacity-100">
                      <span className="text-white font-bold text-sm">View</span>
                    </div>
                  </button>
                ))}
              </div>
            </div>

            {/* Right Button */}
            <button
              onClick={() => scroll("right")}
              className="flex-shrink-0 bg-accent hover:bg-accent-dark text-primary p-3 rounded-full shadow-lg transition-all"
            >
              <ChevronRight size={24} />
            </button>
          </div>
        </div>
      </div>

      {/* Lightbox Modal */}
      {selectedImage && (
        <div className="fixed inset-0 bg-black/80 flex items-center justify-center p-4 z-50">
          <div className="relative max-w-4xl w-full">
            <button
              onClick={() => setSelectedImage(null)}
              className="absolute -top-12 right-0 text-white hover:text-accent transition-colors"
            >
              <X size={32} />
            </button>
            <img
              src={selectedImage.url || "/placeholder.svg"}
              alt={selectedImage.alt}
              className="w-full h-auto rounded-lg shadow-2xl"
              onError={(e) => {
                e.target.src = `/placeholder.svg?height=600&width=800&query=${selectedImage.alt}`
              }}
            />
            <p className="text-white text-center mt-4 text-lg font-semibold">{selectedImage.alt}</p>
          </div>
        </div>
      )}
    </section>
  )
}
